.PhotographersList{grid-area:PhotographersList}.PhotographersList .PhotographersItemList{border:1px solid #C2C2E5;list-style-type:none;padding:0;margin:0}.PhotographersList .PhotographersItemList .PhotographerItem{padding:10px 20px;cursor:pointer}.PhotographersList .PhotographersItemList .PhotographerItem:hover{background-color:#E6E6FA}.PhotographersList .PhotographersItemList .PhotographerItem+.PhotographerItem{border-top:1px solid #D8D8FF}.PhotographersList .PhotographersItemList .activeItem{background-color:#cfcfe1}

.Spinner{width:200px;height:200px;margin:100px auto;overflow:hidden;background:#fff}.Spinner .SpinnerElements{width:100%;height:100%;position:relative;-webkit-transform:translateZ(0) scale(1);transform:translateZ(0) scale(1);-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform-origin:0 0;transform-origin:0 0}@-webkit-keyframes SpinnerElements{0%{opacity:1}100%{opacity:0}}@keyframes SpinnerElements{0%{opacity:1}100%{opacity:0}}.Spinner .SpinnerElements div{box-sizing:content-box;left:98px;top:53px;position:absolute;-webkit-animation:SpinnerElements linear 1s infinite;animation:SpinnerElements linear 1s infinite;background:#0a0a0a;width:4px;height:18px;border-radius:1.98px / 1.98px;-webkit-transform-origin:2px 47px;transform-origin:2px 47px}.Spinner .SpinnerElements div:nth-child(1){-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-animation-delay:-0.8333333333333334s;animation-delay:-0.8333333333333334s;background:#0a0a0a}.Spinner .SpinnerElements div:nth-child(2){-webkit-transform:rotate(60deg);transform:rotate(60deg);-webkit-animation-delay:-0.6666666666666666s;animation-delay:-0.6666666666666666s;background:#0a0a0a}.Spinner .SpinnerElements div:nth-child(3){-webkit-transform:rotate(120deg);transform:rotate(120deg);-webkit-animation-delay:-0.5s;animation-delay:-0.5s;background:#0a0a0a}.Spinner .SpinnerElements div:nth-child(4){-webkit-transform:rotate(180deg);transform:rotate(180deg);-webkit-animation-delay:-0.3333333333333333s;animation-delay:-0.3333333333333333s;background:#0a0a0a}.Spinner .SpinnerElements div:nth-child(5){-webkit-transform:rotate(240deg);transform:rotate(240deg);-webkit-animation-delay:-0.16666666666666666s;animation-delay:-0.16666666666666666s;background:#0a0a0a}.Spinner .SpinnerElements div:nth-child(6){-webkit-transform:rotate(300deg);transform:rotate(300deg);-webkit-animation-delay:0s;animation-delay:0s;background:#0a0a0a}

.AlbumsList{grid-area:AlbumsList}.AlbumsList .AlbumsItemList{border:1px solid #C2C2E5;list-style-type:none;padding:0;margin:0}.AlbumsList .AlbumsItemList .AlbumItem{padding:10px 20px;cursor:pointer}.AlbumsList .AlbumsItemList .AlbumItem:hover{background-color:#E6E6FA}.AlbumsList .AlbumsItemList .AlbumItem+.AlbumItem{border-top:1px solid #D8D8FF}.AlbumsList .AlbumsItemList .activeItem{background-color:#cfcfe1}

.SelectedImage{text-align:center;margin-bottom:1vw}

.Images{grid-area:Images}.Images .ImagesList{display:flex;flex-wrap:wrap}.Images .ImagesList .ItemBlock{text-align:center;flex-grow:1}.Images .ImagesList .ItemBlock .ImageItem{margin-bottom:15px;cursor:pointer}.Images .ImagesList .ItemBlock .SelectedImg{position:relative;display:inline-block}.Images .ImagesList .ItemBlock .SelectedImg:after{content:'';position:absolute;top:0;left:0;right:0;bottom:19px;background:rgba(0,0,0,0.6);opacity:1;cursor:pointer}.Images .Spinner{margin-top:0}

.App{width:95%;max-width:1200px;margin:30px auto;display:grid;grid-template-columns:3fr 5fr;grid-template-areas:"PhotographersList AlbumsList" "Images Images";grid-gap:2vw}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
