.AlbumsList {
  grid-area: AlbumsList;

  .AlbumsItemList {
    border: 1px solid #C2C2E5;
    list-style-type: none;
    padding: 0;
    margin: 0;

    .AlbumItem {
      padding: 10px 20px;
      cursor: pointer;

      &:hover {
        background-color: #E6E6FA;
      }

      & + .AlbumItem {
        border-top: 1px solid #D8D8FF ;
      }
    }
    
    .activeItem {
      background-color: #cfcfe1;
    }
  }
}